/* Box sizing rules */
*,
*::before,
*::after {
    box-sizing: border-box;
}

/* Set core body defaults */
body {
    min-height: 100vh;
    scroll-behavior: smooth;
    text-rendering: optimizeSpeed;
    line-height: 1.5;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
        'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
        'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
    margin: 0;
}

/* Remove default padding */
ul[class],
ol[class] {
    padding: 0;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul[class],
ol[class] {
    list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
    -webkit-text-decoration-skip: ink;
            text-decoration-skip-ink: auto;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
    font: inherit;
}

/* Make images easier to work with */
img {
    max-width: 100%;
    display: block;
}

/* Remove all animations and transitions for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
    * {
        -webkit-animation-duration: 0.01ms !important;
                animation-duration: 0.01ms !important;
        -webkit-animation-iteration-count: 1 !important;
                animation-iteration-count: 1 !important;
        -webkit-transition-duration: 0.01ms !important;
                transition-duration: 0.01ms !important;
        scroll-behavior: auto !important;
    }
}

body{font-size:62.5%}.App{width:100%;max-width:900px;display:grid;margin:0 auto;color:#333}@media (max-width: 1024px){.App{max-width:1000px}}.App>*{width:100%;padding:10px 16px;margin:0 auto}.App .App-header{background:#375077;text-align:left;font-size:2em;font-weight:600;color:#fff}.App .App-main{background:#f7f7f7;text-align:center;font-size:1.4em;padding:24px;max-width:100vw}.App .App-footer{text-align:right;font-size:1.2em;color:#999}

.inputBox{margin-bottom:20px;padding:10px;border:solid 1px #999;border-radius:4px}.inputBox form{display:grid;grid-template-rows:40px 30px;grid-template-columns:140px auto 40px;grid-template-areas:"inText inText subButton" "option1 option2 option2";grid-gap:10px;align-items:stretch;justify-items:stretch}@media (max-width: 370px){.inputBox form{grid-template-rows:40px 30px 30px 40px;grid-template-columns:auto 40px;grid-template-areas:"inText inText" "option1 option1" "option2 option2" "empty subButton"}}.inputBox form .todoInputText{grid-area:inText;padding:0 10px;border-radius:6px;border:solid 1px #375077;background-color:#fff;color:#375077;font-size:1.4em}@media (max-width: 370px){.inputBox form .todoInputText{font-size:1.2em}}.inputBox form .todoInputButton{grid-area:subButton;background-color:#df5c74;padding:0;border-radius:6px;border:none;color:#f7f7f7;font-size:2em;line-height:0;-webkit-transition:-webkit-transform 100ms;transition:-webkit-transform 100ms;transition:transform 100ms;transition:transform 100ms, -webkit-transform 100ms}.inputBox form .todoInputButton:hover{-webkit-transform:translateY(2px);transform:translateY(2px)}.inputBox form .inputOptionPrio{grid-area:option2;align-self:center;justify-self:start}.inputBox form .inputOptionCat{grid-area:option1}.inputBox form .inputOptionCat select{width:100%;height:100%;padding:3px 8px;border-radius:4px;border:1px solid #999;color:#375077}

.TodoListCatsList{display:block;width:100%;list-style:none;text-align:left;position:relative;bottom:-1px}.TodoListCatsList .TodoListCat{display:inline-block;border:solid 1px #999;border-bottom:none;padding:6px 12px;border-radius:4px;border-bottom-left-radius:0;border-bottom-right-radius:0;color:#999;z-index:10}.TodoListCatsList .TodoListCat:hover{color:#375077}.TodoListCatsList .TodoListCat.currentCat{background-color:#fff;color:#375077;z-index:30}.TodoListCatsList .TodoListCat+.TodoListCat{margin-left:6px}.TodoListCatsList *{vertical-align:sub}.TodoListCatsListBottom{display:block;width:100%;height:3px;background-color:#fff;color:#375077;border:solid 1px #999;border-bottom:none;z-index:20}

.todoListItem{width:100%;height:40px;margin:10px 0;display:flex;flex-flow:row nowrap;justify-content:space-between;align-items:center;line-height:1.5em}.todoListItem:hover{background-color:#f7f7f7}.todoListItem .checkBox{padding:0;margin-right:6px;background:none;border:none;color:#999;font-size:1.4em}.todoListItem .checkBox:hover{color:#375077}.todoListItem .catLabel{min-width:60px;max-width:120px;padding:4px 10px;margin-right:6px;overflow:hidden;white-space:nowrap;text-overflow:ellipsis;border-radius:4px;background-color:#f7f7f7;color:#375077;text-align:center}@media (max-width: 370px){.todoListItem .catLabel{padding:4px 4px}}.todoListItem .todoText{flex:1 1 70%;overflow:hidden;white-space:nowrap;text-overflow:ellipsis;color:#333}.todoListItem .editButton{padding:0;margin-left:6px;border:none;background:none;color:#999;font-size:1.4em}.todoListItem .editButton:hover{color:#375077}.todoListItem .deleteButton{margin-left:6px;padding:0;border:none;background:none;color:#999;font-size:1.4em}.todoListItem .deleteButton:hover{color:#df5c74}.todoChecked_true .todoText{text-decoration:line-through}.todoListPriority1 .checkBox{color:#df5c74}.todoListPriority2 .checkBox{color:#375077}.todoListPriority3 .checkBox{color:#999}

.TodoListPage{display:block;width:100%;padding:20px;background-color:#fff;color:#375077;border:solid 1px #999;border-top:none;min-height:400px}.TodoListPage ul{list-style:none;text-align:left;padding:0;margin:0}

.modalWrapper{position:fixed;z-index:50;width:100vw;height:100vh;left:0;top:0}.modalWrapper.modalTrue{display:block}.modalWrapper.modalFalse{display:none}.modalWrapper .catsModal{position:absolute;z-index:100;width:85%;height:auto;max-width:500px;min-height:400px;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);background-color:#fff;border:1px solid #999;border-radius:6px;box-shadow:0 0 20px 0 rgba(0,0,0,0.25);padding:20px}.modalWrapper .catsModal .closeModal{position:absolute;right:10px;top:10px;border:none;background:none;color:#999;font-size:1.6em}.modalWrapper .catsModal .closeModal:hover{color:#375077}.modalWrapper .catsModal h3{display:block;text-align:left;color:#375077}.modalWrapper .catsModal form{margin:20px auto;padding:10px;background-color:#f7f7f7}.modalWrapper .catsModal form .catAdd{padding:0;margin-left:6px;border:none;background:none;color:#df5c74;font-size:1.6em;vertical-align:middle}.modalWrapper .catsModal form .catAdd:hover{-webkit-transform:translateY(2px);transform:translateY(2px)}.modalWrapper .modalBg{position:absolute;z-index:50;width:100%;height:100%;left:0;top:0;background-color:rgba(0,0,0,0.25)}

ul.catsList{display:block;width:100%;margin:20px auto;text-align:left;list-style:none}ul.catsList li.catsListItem{height:38px;display:flex;padding:6px;flex-flow:row nowrap;align-content:space-between}ul.catsList li.catsListItem span{flex:1 1 70%;overflow:hidden;white-space:nowrap;text-overflow:ellipsis;color:#333}ul.catsList li.catsListItem .editButton{padding:0;margin-left:6px;border:none;background:none;color:#999;font-size:1.4em}ul.catsList li.catsListItem .editButton:hover{color:#375077}ul.catsList li.catsListItem .deleteButton{margin-left:6px;padding:0;border:none;background:none;color:#999;font-size:1.4em}ul.catsList li.catsListItem .deleteButton:hover{color:#df5c74}ul.catsList li.catsListItem+ul.catsList li.catsListItem{border-top:1px solid red}

